.project {
    margin-top: 2em;
    padding-top: 2em;
    padding-bottom: 2em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 5em;
    background-color: #fff;
}

.description {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.marginaround {
    margin-top: 0em;
    margin-bottom: 0em;
    margin-left: 0em;
    margin-right: 0em;
    margin: 0.5em;
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.projectmargin {
    margin-bottom: 1rem;
}

.modalwindow {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.modalimage {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: contain;
    /* background-color: lightgray; */
}

.closebutton {
    display: flex !important;
    min-height: 35px;
    min-width: 35px;
    height: 35px;
    width: 35px;
    align-items: center;
    justify-content: center;
    padding: 0;
}